body {
  color: #fff;
  background-color: #dc2a1c;
  margin: 0;
  padding: 0;
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
}

.navbar {
  background-color: #dc2a1c;
  border-bottom: 1px solid #000;
}

.navbar-brand h1 {
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
}

.carousel-item img {
  object-fit: cover;
  height: 499px;
}

.gallery-image {
  object-fit: cover;
  border-radius: 8px;
  width: 100%;
  height: 200px;
}

.text-left, .text-right {
  text-align: left;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  display: flex;
}

.text-left .section-text, .text-right .section-text {
  text-align: justify;
}

.section-title {
  color: #fff;
  margin-bottom: 20px;
  font-size: 2rem;
}

.section-text {
  color: #fff;
  margin-bottom: 30px;
  line-height: 1.6;
}

.video-container video {
  border-radius: 25px;
  transition: transform .3s ease-in-out;
}

.video-container video:hover {
  transform: scale(1);
}

.img-container img {
  border-radius: 15px;
  max-width: 80%;
  height: auto;
  transition: transform .3s ease-in-out;
}

.img-container:hover img {
  transform: scale(1.05);
}

.container-fluid {
  padding: 50px 15px;
}

footer {
  color: #fff;
  text-align: center;
  background-color: #12336d;
  padding: 30px 0;
}

footer h5, footer p {
  margin: 0;
}

footer p {
  font-size: .9rem;
}

.section-title {
  font-size: 2.5rem;
}

video, img {
  max-width: 100%;
  height: auto;
}

.location-card {
  text-align: center;
  background-color: #fff;
  border-radius: 10px;
  margin-bottom: 30px;
  padding: 20px;
  transition: transform .3s ease-in-out, box-shadow .3s;
  box-shadow: 0 4px 6px #0000001a;
}

.location-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 12px #0003;
}

.location-card h5 {
  color: #dc2a1c;
  margin-bottom: 10px;
  font-size: 1.25rem;
}

.location-card p {
  color: #333;
  margin-bottom: 15px;
  font-size: .9rem;
}

.location-card .btn {
  color: #fff;
  background-color: #dc2a1c;
  border-radius: 5px;
  padding: 10px 15px;
  font-size: .9rem;
  transition: background-color .3s;
}

.location-card .btn:hover {
  background-color: #b81f14;
}

.link-footer {
  color: #ddbc53;
  font-weight: 500;
  text-decoration: none;
}

.cover-container {
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  width: 80%;
  height: 60vh;
  margin: 0 auto;
  display: flex;
  position: relative;
  overflow: hidden;
}

.cover-image {
  object-fit: cover;
  object-position: center;
  border-radius: 15px;
  width: 100%;
  height: 100%;
}

.content-container {
  text-align: center;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  text-transform: uppercase;
  margin-bottom: 20px;
  font-size: 2.5rem;
  font-weight: bold;
}

ul li:hover {
  color: #06c;
  cursor: pointer;
  transition: color .3s;
}

#cookie-banner {
  color: #fff;
  text-align: center;
  z-index: 1000;
  background-color: #333;
  padding: 15px;
  font-size: 14px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}

#cookie-banner a {
  color: #f90;
  text-decoration: none;
}

#cookie-banner button {
  cursor: pointer;
  background-color: #f90;
  border: none;
  margin-left: 10px;
  padding: 8px 15px;
  font-size: 14px;
}

#cookie-banner button:hover {
  background-color: #cc7a00;
}

@media (width <= 767px) {
  .section-title {
    font-size: 1.8rem;
  }

  .navbar-brand h1 {
    font-size: 1.5rem;
  }

  .carousel-inner img {
    object-fit: cover;
    height: 250px;
  }

  #cookie-banner {
    font-size: .8rem;
  }

  .video-container video, .img-container img {
    width: 100%;
    height: auto;
  }
}

@media (width >= 768px) and (width <= 1024px) {
  .carousel-inner img {
    height: 400px;
  }

  .section-title {
    font-size: 2rem;
  }
}

@media (width >= 1025px) {
  .carousel-inner img {
    height: 499px;
  }

  .section-title {
    font-size: 2.5rem;
  }

  .section-text {
    font-size: 1rem;
  }
}
